/* Your app custom styles here */
:root {
  --custom-color: #EE350F;
  --color-green: #4cd964;
  --zmp-list-button-text-align: center;
  --zmp-popup-tablet-border-radius: 15px;
  --zmp-bars-bg-color: #fff;
  --zmp-navbar-height: 64px;
  --zmp-navbar-large-title-height: 56px;
  --zmp-block-inset-border-radius: 10px;
  --zmp-color-picker-popover-width: 240px;
  --zmp-color-picker-sb-spectrum-height: 180px;
  --zmp-input-outline-border-color: #ccc;
}
.theme-dark {
  --zmp-bars-bg-color: #1b1b1b;
}
.ios .list .icon-radio {
  border: 1px solid var(--zmp-checkbox-inactive-color);
  border-radius: 50%;
}
.ios .list input:checked ~ .icon-radio {
  border-color: var(--zmp-theme-color);
}
.navbar .title {
  display: inline-flex;
  align-items: center;
}
.navbar .title-large-text {
  display: flex;
  align-items: center;
}
.zmp-navbar-logo {
  background: url(../images/logo.png) no-repeat center;
  background-size: contain;
  width: 36px;
  height: 36px;
  display: block;
  margin-right: 10px;
  flex-shrink: 0;
}
.title-large .zmp-navbar-logo {
  width: 48px;
  height: 48px;
}
.create-app-icon {
  padding-right: 32px;
  text-align: center;
}
.create-app-icon img {
  width: 100%;
  height: auto;
  border-radius: 16%;
  border: 1px solid rgba(0, 0, 0, 0.1);
  display: block;
}
.create-app-icon label {
  display: block;
  position: relative;
  cursor: pointer;
  overflow: hidden;
  margin-bottom: 0.5em;
}
.create-app-icon label input {
  position: absolute;
  right: 0;
  top: 0;
  visibility: hidden;
  opacity: 0;
}
.create-app-icon small {
  line-height: 1.2;
  opacity: 0.5;
}
.label-file-input {
  overflow: hidden;
}
.label-file-input input {
  position: absolute;
  opacity: 0;
  visibility: hidden;
  right: 0;
  top: 0;
  font-size: 100px;
}
i.block-icon {
  color: #fff;
  font-size: 28px !important;
  width: 40px;
  height: 40px;
  border-radius: calc(10px - 2px);
  border-radius: calc(var(--zmp-block-inset-border-radius) - 2px);
  line-height: 40px;
  background: #3e444c;
  display: block;
  background-repeat: no-repeat;
  background-position: center;
  flex-shrink: 0;
}
i.block-icon.block-icon-webpack {
  background-image: url(data:image/svg+xml;base64,);
  background-size: 28px auto;
}
.inputs-list {
  --zmp-label-font-size: 14px;
}
.inputs-list .item-input-wrap {
  margin-top: 5px;
}
.inputs-list .item-label {
  margin-bottom: 2px;
}
.content-block {
  --zmp-block-title-text-color: #333;
  --zmp-block-padding-horizontal: 20px;
  --zmp-block-padding-vertical: 20px;
  --zmp-list-item-padding-horizontal: 20px;
  --zmp-list-media-item-padding-horizontal: 20px;
  --zmp-list-item-text-max-lines: 20;
  --zmp-list-border-color: transparent;
  --zmp-list-bg-color: transparent;
}
.theme-dark .content-block {
  --zmp-block-title-text-color: #fff;
}
.content-block .block-title,
.content-block .list,
.content-block .block {
  --zmp-block-margin-vertical: 20px;
}
.content-block .block:last-child,
.content-block .list:last-child {
  margin-bottom: 0;
}
.content-block .block-title {
  margin-left: 0;
  margin-right: 0;
}
.content-block .list {
  margin-left: calc(-1 * var(--zmp-block-padding-horizontal));
  margin-right: calc(-1 * var(--zmp-block-padding-horizontal));
}
.content-block .block-title-large {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.content-block .block-title-large i + span {
  margin-left: 15px;
}
.content-block .block-title-large .right {
  margin-left: auto;
  align-self: center;
  display: flex;
  align-items: center;
  flex-shrink: 0;
}
.content-block .block-title-large .toggle-label {
  font-size: 14px;
  margin-right: 5px;
  font-weight: normal;
}
.checkbox-row,
.radio-row {
  margin: 10px 0;
}
.checkbox-row:last-child,
.radio-row:last-child {
  margin-bottom: 0;
}
.checkbox-col,
.radio-col {
  --checkbox-col-text-color: rgba(0, 0, 0, 0.5);
  text-align: center;
  color: var(--checkbox-col-text-color);
}
.theme-dark .checkbox-col,
.theme-dark .radio-col {
  --checkbox-col-text-color: rgba(255, 255, 255, 0.5);
}
.checkbox-col .col-icon,
.radio-col .col-icon,
.checkbox-col .col-icon img,
.radio-col .col-icon img,
.checkbox-col .col-label,
.radio-col .col-label {
  transition-duration: 200ms;
}
.checkbox-col .col-label,
.radio-col .col-label {
  font-weight: 500;
  line-height: 1.3;
}
.checkbox-col .col-label small,
.radio-col .col-label small {
  display: block;
  line-height: 1.2;
}
.theme-dark .checkbox-col .col-icon,
.theme-dark .radio-col .col-icon {
  background: rgba(255, 255, 255, 0.05);
}
.checkbox-col .col-icon,
.radio-col .col-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
  border: 4px solid transparent;
  border-radius: 10px;
  margin-bottom: 5px;
  margin-left: auto;
  margin-right: auto;
  background: rgba(0, 0, 0, 0.05);
  cursor: pointer;
  position: relative;
}
.checkbox-col .col-icon .text-icon,
.radio-col .col-icon .text-icon {
  font-size: 28px;
  line-height: 1;
  font-weight: bold;
}
.checkbox-col .col-icon i,
.radio-col .col-icon i {
  font-size: 28px;
}
.checkbox-col .col-icon svg,
.radio-col .col-icon svg {
  height: 28px;
}
.checkbox-col .col-icon img,
.radio-col .col-icon img {
  height: 28px;
  opacity: 0.5;
}
.checkbox-col .col-icon img + img,
.radio-col .col-icon img + img {
  margin-left: -10px;
}
.checkbox-col.checked,
.radio-col.checked {
  --checkbox-col-text-color: var(--zmp-theme-color);
}
.checkbox-col.checked .col-icon,
.radio-col.checked .col-icon {
  border-color: var(--zmp-theme-color);
  background: rgba(var(--zmp-theme-color-rgb), 0.02);
}
.checkbox-col.checked .col-icon img,
.radio-col.checked .col-icon img {
  opacity: 1;
}
.checkbox-template-col .col-icon {
  padding: 0;
  background: none;
  overflow: hidden;
  border-color: #f1f1f1;
}
.checkbox-template-col .col-icon img {
  width: 100%;
  height: auto;
  display: block;
  -webkit-filter: grayscale(1);
          filter: grayscale(1);
}
.checkbox-template-col.checked .col-icon img {
  -webkit-filter: grayscale(0);
          filter: grayscale(0);
}
.home-cards {
  --zmp-grid-gap: 30px;
}
.home-cards .card {
  margin: 0;
  margin-bottom: 30px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
  border-radius: 15px;
  overflow: hidden;
}
.home-cards .card-content {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 500;
  overflow: hidden;
  height: 100px;
}
.center-content {
  max-width: 960px;
  margin-left: auto;
  margin-right: auto;
}
.title-large {
  justify-content: center;
}
.title-large-text {
  max-width: 960px;
}
.block-title {
  white-space: normal;
}
pre {
  padding: 15px;
  white-space: pre-wrap;
  word-break: break-all;
  font-weight: bold;
  background: var(--zmp-page-bg-color);
  font-size: 13px;
}
pre.round {
  border-radius: 10px;
  border-radius: var(--zmp-block-inset-border-radius);
}
.popup-log {
  background: transparent;
}
.popup-log pre {
  margin: 0;
  height: 100%;
  overflow: auto;
  box-sizing: border-box;
  background: rgba(28, 28, 29, 0.965);
  color: #fff;
}
.drag-area {
  border: 2px dashed #aaa;
  display: block;
  cursor: pointer;
}
.drag-area input {
  position: absolute;
  visibility: hidden;
}
.drag-area .block-header,
.drag-area .block-footer {
  text-align: center;
}
.drag-area .asset-preview {
  width: 150px;
  height: 150px;
  margin-left: auto;
  margin-right: auto;
}
.drag-area .asset-preview img {
  max-width: 100%;
  max-height: 100%;
}
.drag-area.dragenter {
  background-color: rgba(var(--zmp-theme-color-rgb), 0.1);
}
.button.loading .icon {
  animation: rotate 2s infinite linear;
}
.button-block {
  display: flex;
  justify-content: center;
}
.button.button-center-content {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 300px;
  max-width: 100%;
}
@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
